import useWindowDimensions from 'assets/features/getScreen';
import Button from 'devextreme-react/button';
import {
  Column, Lookup, Pager,
  Paging, Scrolling
} from 'devextreme-react/data-grid';
import 'devextreme/data/odata/store';
import React, { useRef } from 'react';
import KeepAlive from 'react-activation';
import { useHistory } from "react-router-dom";
import { useDispatch } from 'react-redux';
import { addParamsToState } from 'store/module/paramsComponent';
import { addMenuRouter } from 'store/module/tagViews';
import { DataGrid } from 'pages/index';
export interface IObject {
  [key: string]: Object;
}
const dataSource = {
  store: {
      type: 'odata',
      key: 'Task_ID',
      url: 'https://js.devexpress.com/Demos/DevAV/odata/Tasks'
  },
  expand: 'ResponsibleEmployee',
  select: [
      'Task_ID',
      'Task_Subject',
      'Task_Start_Date',
      'Task_Due_Date',
      'Task_Status',
      'Task_Priority',
      'Task_Completion',
      'ResponsibleEmployee/Employee_Full_Name'
  ]
};

export default function Task() {
  const dispatch = useDispatch();
  const history = useHistory();
  const stageCanvasRef: any = useRef();
  const { current } = stageCanvasRef;
  const isHeight = current?.props?.height;
  // const 
  // console.log(isHeight);
  //on ni x l ci li  di theo 
  const handleOnDbClickCell = (item: any): void => {
    // console.log(item);
    const index: string = 'benhan_' + item?.key;
    const newObject: IObject = {};
    const newObjectRoute: IObject = {};
    newObjectRoute['meta'] = { icon: 'tab', text: 'BenhAn' + item?.key, path: '/benhan/' + item?.key };
    newObject[index] = item.values;
    dispatch(addParamsToState(newObject));
    dispatch(addMenuRouter([newObjectRoute]))
    history.push({
      pathname: '/benhan/' + item?.key,
      state:item.values
  });
  }

  return (
    <KeepAlive>
      <React.Fragment>
        <div className="left-side">
          <div className="logo">
            <span style={{ fontWeight: "bold" }}>Danh sch bnh nhn</span>
            &nbsp;
            <Button
              type="success"
              text="Reset vn tay"
              width={"auto"}
            // onClick={this.doneClick} 
            />
          </div>
        </div>
          <DataGrid column={[{ caption: 'M BN', width: 100 }, { caption: 'H lt'}, { caption: 'Tn'}, { caption: 'Ngy sinh', width: 200 }, { caption: 'Nm sinh', width: 100 }, { caption: 'Gii tnh', width: 100 }, { caption: 'in thoi 1', width: 100 }, { caption: 'in thoi 2', width: 100 }, { caption: 'a ch', width: 200 }, { caption: 'Ngi lin h', width: 150 }, { caption: 'Quan h vi BN', width: 100 }, { caption: 'T ngi LH', width: 100 }, { caption: 'DT ngi LH', width: 100 }, { caption: 'Ghi ch', width: 100 }, { caption: 'S BHYT', width: 100 }]} propertyHeight={"400"} onCellDblClick={handleOnDbClickCell} dataSource={dataSource}  />
      </React.Fragment>
    </KeepAlive>
  )
}

